<template>
  <div id="page-user-view">

    <vs-alert color="danger" title="Order Not Found" :active.sync="order_not_found">
      <span>{{ $t('order.notFound.message', { orderId: $route.params.orderId }) }} </span>
    </vs-alert>

    <div id="user-data" v-if="order_data">

      <vx-card title="Order" class="mb-base">

        <div class="vx-row">

          <!-- Information - Col 1 -->
          <div class="vx-col flex-1" id="account-info-col-1">
            <table>
              <tr>
                <td class="font-semibold">ID</td>
                <td>{{ order_data.id }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('batch.batch') }}</td>
                <td>{{ order_data.batchesId }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 1 -->

          <!-- Information - Col 2 -->
          <div class="vx-col flex-1" id="account-info-col-2">
            <table>
              <tr>
                <td class="font-semibold">{{ $t('order.paymentMethod') }}</td>
                <td>{{ paymentMethod }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('order.paymentStatus') }}</td>
                <td>{{ paymentStatus }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ $t('order.paymentValue') }}</td>
                <td>{{ paymentValue }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 2 -->
          <!-- <div class="vx-col w-full flex" id="account-manage-buttons">
            <vs-button icon-pack="feather" icon="icon-edit" class="mr-4" :to="{name: 'admin-order-edit', params: { orderId: $route.params.orderId }}">Edit</vs-button>
          </div> -->
          <!-- <div class="vx-col w-full flex" id="account-manage-buttons">
          </div> -->

        </div>

      </vx-card>

      <!-- Permissions -->
      <vx-card>

        <div class="vx-row">
          <div class="vx-col w-full">
            <div class="flex items-end px-3">
              <feather-icon svgClasses="w-6 h-6" icon="BookIcon" class="mr-2" />
              <span class="font-medium text-lg leading-none">{{ $t('book.books') }}</span>
            </div>
            <vs-divider />
          </div>
        </div>

        <div class="block overflow-x-auto">
          <table class="w-full permissions-table">
            <tr>
              <th class="font-semibold text-base text-left px-3 py-2" v-for="heading in [`${$t('book.isbn')}`, `${$t('book.title')}`, `${$t('book.author')}`, `${$t('book.publishingHouse')}`, `${$t('book.price')}`]" :key="heading">{{ heading }}</th>
            </tr>

            <tr v-for="(book, index) in order_data.books" :key="index">
              <td class="px-3 py-2">{{ book.attributes.isbn }}</td>
              <td class="px-3 py-2">{{ book.attributes.title }}</td>
              <td class="px-3 py-2">{{ book.attributes.author }}</td>
              <td class="px-3 py-2">{{ book.attributes.publishingHouse }}</td>
              <td class="px-3 py-2">{{ book.attributes.price.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) }}</td>
            </tr>
          </table>
        </div>

      </vx-card>

    </div>
  </div>
</template>

<script>
import moduleOrderManagement from '@/store/order-management/moduleOrderManagement.js'

export default {
  data() {
    return {
      order_data: {},
      order_not_found: false,
    }
  },
  computed: {
    paymentStatus() {
      switch (this.order_data.paymentStatus) {
        case 0:
          return this.$t('order.payment.status.waiting')
        case 1:
          return this.$t('order.payment.status.paid')
        case 2:
          return this.$t('order.payment.status.pending')
        case 3:
          return this.$t('order.payment.status.expired')
        default:
          return this.order_data.paymentStatus
      }
    },
    paymentMethod() {
      switch (this.order_data.paymentMethod) {
        case 1:
          return this.$t('order.payment.method.slip')
        case 2:
          return this.$t('order.payment.method.credit')
        default:
          return this.order_data.paymentMethod
      }
    },
    paymentValue() {
      return this.order_data.paymentValue.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
    },
  },
  methods: {
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: `Confirm Delete`,
        text: `You are about to delete "${this.order_data.username}"`,
        accept: this.deleteRecord,
        acceptText: "Delete"
      })
    },
    deleteRecord() {
      /* Below two lines are just for demo purpose */
      this.$router.push({name:'app-user-list'});
      this.showDeleteSuccess()

      /* UnComment below lines for enabling true flow if deleting user */
      // this.$store.dispatch("orderManagement/removeRecord", this.order_data.id)
      //   .then(()   => { this.$router.push({name:'app-user-list'}); this.showDeleteSuccess() })
      //   .catch(err => { console.error(err)       })
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: 'success',
        title: 'User Deleted',
        text: 'The selected user was successfully deleted'
      })
    }
  },
  created() {
    // Register Module UserManagement Module
    if(!moduleOrderManagement.isRegistered) {
      this.$store.registerModule('orderManagement', moduleOrderManagement)
      moduleOrderManagement.isRegistered = true
    }

    const orderId = this.$route.params.orderId
    this.$store.dispatch("orderManagement/fetchOrder", orderId)
      .then(res => { this.order_data = res.data.attributes })
      .catch(err => {
        if(err.response.status === 404) {
          this.order_not_found = true
          return
        }
        console.error(err) })
  }
}

</script>

